body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.tabs-extra-logo {
  margin-right: 16px;
}

.ant-row-rtl .tabs-extra-button {
  margin-right: 0;
  margin-left: 16px;
}

.logo-short {
  margin-top: 5px;
}

.multi-button {
  height: auto;
  width: 345px;
  text-align: left;
}

.popup {
  position: absolute;
  transform: translate(-50%, -100%);
  display: flex;
  flex-direction: column;
  min-width: 200px;
}

.popup-content {
  line-height: 6px;
  padding: 10px;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(38, 38, 38, 0.2);
}

.popup-content span {
  font-weight: 600;
  padding-left: 5px;
}

.popup-close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 5px;
  font-size: 12px;
  cursor: pointer;
}

.popup-tip {
  width: 0;
  height: 0;
  align-self: center;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #fff;
}

.layerslist {
  list-style-type: none;
  padding-left: 0px;
  overflow-y: scroll;
  height: calc(100vh - 290px);
}

.extraLayer span {
  margin-left: 8px;
}

.titleLayer {
  text-transform: capitalize;
  font-weight: 700;
  color: blueviolet;
}